}

.necessity-list-block {
  display: inline-flex;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 15px 0px;
  padding-bottom: 15px;
  overflow-y: auto;
}

.necessity-item-block {
  width: 160px;
  height: 106.8px;
  display: flex;
  align-content: space-around;
  overflow-y: auto;
  border-radius: 12px;
  border: solid 0.5px #828282;
  background-color: #ffffff;
}

.necessity-template-block {
    width: 512px;
    height: 610px;
    overflow-y: auto;
    position: relative;

    background: white;
    border-radius: 16px;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.04);

    margin: 0 auto;

    margin-top: 10px;
    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
}

